@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');

.login-container {
  min-height: 100vh;
}

.form-section {
  flex: 1;
  background-color: #ffffff;
}

.logo-section {
  flex: 1;
  background-color: #007bff;
  color: white;
  font-family: 'Poppins', sans-serif;

  .logo-text {
    font-size: 3rem;
    font-weight: 600;
    text-align: center;
  }
}

// Responsive : cacher la partie droite sur petits Ã©crans
@media (max-width: 768px) {
  .logo-section {
    display: none !important;
  }

  .form-section {
    flex: 1 1 100%;
    padding: 2rem;
  }
}
